import React from "react";
import './Navbar.css'

import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import InstagramIcon from '@mui/icons-material/Instagram';
import FacebookIcon from '@mui/icons-material/Facebook';

import { NavLink } from "react-router-dom";

function NavBar({ loggedIn, logout }) {
    return (
        <nav className="navbar navbar-expand-lg bg-body-tertiary Navbar">
            <div className="container-fluid">
                <NavLink className="navbar-brand ms-5 Navbar-title" to="/">IG SHOP</NavLink>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNavDropdown">
                    <ul className="navbar-nav ms-5">
                        <li className="nav-item ms-3">
                            <NavLink className="nav-link" aria-current="page" to="/"></NavLink>
                        </li>
                        <li className="nav-item ms-3">
                            <NavLink className="nav-link" to="/productList"></NavLink>
                        </li>
                        <li className="nav-item ms-3">
                            <NavLink className="nav-link" to="/contact"></NavLink>
                        </li>


                        {loggedIn &&
                            <>
                                <li className="nav-item ms-3">
                                    <NavLink className="nav-link" to="/admin/new"></NavLink>
                                </li>
                                <li className="nav-item ms-3">
                                    <NavLink className="nav-link" onClick={logout} style={{ color: "rgba(0,0,0,0.65" }}></NavLink>
                                </li>
                            </>
                        }

                    </ul>
                    <span className="navbar-text ms-auto Navbar-icon">
                        <span className="me-3"><WhatsAppIcon /></span>
                        <span className="me-3"><a href="https://www.instagram.com/"><InstagramIcon /></a></span>
                        <span className="me-3"><FacebookIcon /></span>
                    </span>
                </div>
            </div>
        </nav>
    )
}

export default NavBar;