import React, { useEffect, useState } from 'react';

import NavBar from './Navbar';
import Homepage from './Homepage';
import Footer from './Footer'
import ProductList from './ProductList'
import Product from './Product'
import Contact from './Contact'
import NewProduct from './AdminComponent/NewProduct';
import Login from './AdminComponent/Login';
import EditProduct from './AdminComponent/EditProduct';

import './App.css';
import { Routes, Route, Navigate } from "react-router-dom";

function App() {
  const [products, setProducts] = useState([]);
  const [loggedIn, setIsloggedIn] = useState(false);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    getProducts();
    loginStatus();
    console.log('rerender')
  }, []);

  const getProducts = () => {
    fetch('/api/products')
      .then(response => response.json())
      .then(data => setProducts(data))
      .catch(error => console.error(error));
  }

  const loginStatus = () => {
    fetch('/api/isLoggedIn')
      .then(response => response.json())
      .then(data => {
        data.isLoggedIn ? setIsloggedIn(true) : setIsloggedIn(false)
        setLoading(false)
      })
      .catch(error => console.error(error));
  }

  const logout = () => {
    fetch('/api/adminLogout')
      .then(response => response.json())
      .then(data => {
        setIsloggedIn(false)
      })
      .catch(error => console.error(error));
  }

  const deleteProduct = (id) => {
    fetch(`/api/product/${id}`, {
      method: 'DELETE'
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.message === "deleted") {
          setProducts(products.filter(product => (product._id !== id)))
        }
      })
      .catch((error) => {
        console.error('Error:', error);
      });
  }

  if (loading) {
    return (
      <div>

      </div>
    )
  }
  return (
    <div className="App">
      <NavBar loggedIn={loggedIn} logout={logout} />
      <Routes>
        <Route path="/" element={<Homepage products={products} />} />
        <Route path="/productList" element={<ProductList products={products} />} />
        <Route path="/product/:id" element={<Product deleteProduct={deleteProduct} loggedIn={loggedIn} />} />
        <Route path="/contact" element={<Contact />} />
        <Route path="/admin/new" element={loggedIn ? <NewProduct /> : <Navigate to='/admin/login' />} />
        <Route path="/admin/login" element={<Login setIsloggedIn={setIsloggedIn} />} />
        <Route path="/admin/product/:id/edit" element={loggedIn ? <EditProduct products={products} /> : <Navigate to='/admin/login' />} />
      </Routes>
      <Footer />
    </div>
  );
}
export default App;
