import React from "react";
import Carousel from './Carousel';
import './HomePage.css'
import LittleProduct from './LittleProduct'


import Button from '@mui/material/Button';

import { Link } from "react-router-dom";

function Homepage(props) {
    return (
        <div className="Homepage-container">
            <Carousel />
            <div className="Homepage-hotItem-container">
                <h1 className="Homepage-hotItem-title"></h1>
                <div className="Homepage-hotItem">
                    {props.products.slice(0, 8).map(product => (
                        <LittleProduct {...product} />
                    ))}
                </div>
                <div className="Homepage-more">
                    <Link to='/productList'>
                        <Button size="large" variant="outlined"></Button>
                    </Link>
                </div>
            </div>
        </div>
    )
}

export default Homepage;
