import React, { useEffect, useState } from "react";
import { useParams, useNavigate, Link } from 'react-router-dom';
import './Product.css'
import Button from '@mui/material/Button';

function Product({ deleteProduct, loggedIn }) {
    const [product, setProduct] = useState([])
    const [loading, setLoading] = useState(true)
    const { id } = useParams();

    const history = useNavigate();
    useEffect(() => {
        fetch(`/api/products/${id}`)
            .then(data => data.json())
            .then(data => setProduct(data))
            .then(data => setLoading(false))
    }, [])

    const handleDelete = () => {
        deleteProduct(product._id);
        history("/productList");
        history(0);
    }

    if (loading) {
        return ("")
    }
    return (
        <div className="Product-container">
            <div id="carouselExample" className="carousel slide Product-carousel">
                <div className="carousel-inner" role="listbox" style={{ width: "500px", height: "500px" }}>
                    {product.images.map((image, index) => (
                        <div className={`carousel-item Product-carousel-item ${index === 0 && "active"}`}>
                            <img src={image.url} className="d-block w-100" alt="..." />
                        </div>
                    ))}
                </div>
                {product.images.length > 1 &&
                    <>
                        <button className="carousel-control-prev" type="button" data-bs-target="#carouselExample" data-bs-slide="prev">
                            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span className="visually-hidden">Previous</span>
                        </button>
                        <button className="carousel-control-next" type="button" data-bs-target="#carouselExample" data-bs-slide="next">
                            <span className="carousel-control-next-icon" aria-hidden="true"></span>
                            <span className="visually-hidden">Next</span>
                        </button>
                    </>
                }
            </div>
            <div className="Product-detail">
                <div>
                    <h2>{product.name}</h2>
                    <p>HK${product.price}</p>
                </div>
                <div className="Product-description">
                    <h3></h3>
                    <p>{product.description}</p>
                </div>
            </div >
            {loggedIn &&
                <div>
                    <Button variant="contained" onClick={handleDelete}></Button>
                    <Link to={`/admin/product/${product._id}/edit`}>
                        <Button variant="contained"></Button>
                    </Link>
                </div >
            }
        </div>
    )
}

export default Product;