import React, { useState, useEffect } from "react";
import TransitionAlerts from '../helperComponent/TransitionAlerts'

import { useParams, useNavigate } from 'react-router-dom';
import useInputState from '../hooks/useInputState'
import TextField from '@mui/material/TextField';

import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Button from '@mui/material/Button';
import { MuiFileInput } from 'mui-file-input'
import Checkbox from '@mui/material/Checkbox';

// import './EditProduct.css'


function EditProduct() {
    const { id } = useParams();
    const [product, setProduct] = useState()
    const [name, handleNameChange, resetName, setName] = useInputState("")
    const [price, handleNamePrice, resetPrice, setPrice] = useInputState("")
    const [description, handleDescriptionChange, resetDescription, setDescription] = useInputState("")
    const [category, setCategory] = useState("");
    const [errorMsg, setErrorMsg] = useState("")
    const [open, setOpen] = useState(false);
    const handleCategoryChange = (event) => {
        setCategory(event.target.value);
    };
    const [files, setFiles] = useState([]);
    const [deleteFiles, setDeleteFiles] = useState([]);

    const history = useNavigate();

    useEffect(() => {
        fetch(`/api/products/${id}`)
            .then(data => data.json())
            .then(data => {
                setProduct(data);
                setName(data.name);
                setPrice(data.price);
                setDescription(data.description);
                setCategory(data.category);
            })
    }, [])
    const handleChange = (newFile) => {
        if (newFile) {
            const selectedFiles = Array.from(newFile);
            const allowedTypes = ['image/png', 'image/jpeg', 'image/jpg'];

            const validatedFiles = selectedFiles.filter((file) =>
                allowedTypes.includes(file.type)
            );

            setFiles(validatedFiles);
        }
    };

    const handleCheckBoxChange = (e) => {
        if (e.target.checked) {
            setDeleteFiles([...deleteFiles, e.target.value])
        } else {
            const newDeleteFile = deleteFiles.filter(file => file !== e.target.value)
            setDeleteFiles(newDeleteFile)
        }
    }

    const handleSubmit = (e) => {
        if (!product) {
            return;
        }
        e.preventDefault();
        if (!(name && price && description && category)) {
            setOpen(true)
            setErrorMsg(`: ${!name ? " " : ""}${!price ? " " : ""}${!category ? " " : ""}${!description ? " " : ""}`)
            return;
        }
        let formData = new FormData();
        formData.append('name', name);
        formData.append('price', price);
        formData.append('category', category);
        formData.append('description', description);

        for (let i = 0; i < deleteFiles.length; i++) {
            formData.append('deleteFiles[]', deleteFiles[i]);
        }
        for (let i = 0; i < files.length; i++) {
            formData.append('images', files[i]);
        }

        fetch(`/api/product/${id}/edit`,
            {
                body: formData,
                method: "PUT"
            })
            .then((response) => response.json())
            .then((data) => {
                console.log('Success:', data);
                if (data.message !== "you must be signed in") {
                    history(`/product/${id}`);
                    history(0);
                }
            })
            .catch((error) => {
                console.error('Error:', error);
            });
    }

    if (!product) {
        return (
            <div>
            </div>
        )
    }
    return (
        <div>
            <div className="alert">
                <TransitionAlerts messege={errorMsg} open={open} setOpen={setOpen} />
            </div>
            <form onSubmit={handleSubmit}>
                <TextField
                    value={name}
                    onChange={handleNameChange}
                    id="name"
                    label=""
                    variant="outlined"
                    autoFocus
                />
                <TextField
                    type="number"
                    value={price}
                    onChange={handleNamePrice}
                    id="price"
                    label=""
                    variant="outlined"
                />
                {/* file input */}
                <br />
                <MuiFileInput value={files} onChange={handleChange} multiple placeholder="" margin="normal" />

                <Box sx={{ minWidth: 120 }} className="Products-sideBar-selector">
                    <FormControl fullWidth>
                        <InputLabel id="demo-simple-select-label"></InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={category}
                            label="category"
                            onChange={handleCategoryChange}
                        >
                            <MenuItem value=""></MenuItem>
                            <MenuItem value=""></MenuItem>
                            <MenuItem value=""></MenuItem>
                        </Select>
                    </FormControl>
                </Box>
                <TextField
                    value={description}
                    onChange={handleDescriptionChange}
                    id="description"
                    label=""
                    multiline
                    rows={4}
                    fullWidth
                    margin="normal"
                />
                <div>
                    {product.images.map(image => (
                        <div key={image.filename}>
                            <img src={image.url} />
                            <Checkbox
                                onChange={handleCheckBoxChange}
                                value={image.filename}
                                inputProps={{ 'aria-label': 'controlled' }}
                            />
                        </div>
                    ))}
                </div>
                <Button onClick={handleSubmit} variant="contained" margin="normal" fullWidth></Button>

            </form>

        </div>
    )
}

export default EditProduct;