import React from "react";
import './LittleProduct.css'
import { Link } from "react-router-dom";

function LittleProduct({ images, name, price, _id, deleteProduct }) {
    return (
        <Link to={`/product/${_id}`} className="card LittleProduct" style={{ width: "18rem", border: "none" }}>
            <div className="LittleProduct-imgContainer">
                <img src={images[0].url} className="card-img-top" alt="..." />
            </div>
            <div className="card-body LittleProduct-contentContainer">
                <p className="card-text">{name}</p>
                <p className="card-text">HK${price}</p>
            </div>
        </Link>

    )
}

export default LittleProduct;
