import React, { useEffect, useState } from "react";
import LittleProduct from './LittleProduct'


import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

import Pagination from '@mui/material/Pagination';


import './ProductList.css'

function Products() {
    const [sorting, setSorting] = useState('newest');
    const [category, setCategory] = useState('');
    const [products, setProducts] = useState([]);
    const [displayProducts, setDisplayProducts] = useState([])
    const [currentPage, setCurrentPage] = useState(1)

    let pagesNum = Math.ceil(displayProducts.length / 9);

    const handleSortingChange = (event) => {
        setSorting(event.target.value);
    };

    const handleCategoryChange = (event) => {
        setCategory(event.target.value);
    };

    const handlePageChange = (event, value) => {
        setCurrentPage(value)
    }


    useEffect(() => {
        fetch('/api/products')
            .then(response => response.json())
            .then(data => {
                setProducts(data)
            })
            .catch(error => console.error(error));
    }, []);

    useEffect(() => {
        // Sort the products array based on the selected sorting option
        const sortedProducts = [...products].sort((a, b) => {
            if (sorting === "newest") {
                return new Date(b.createTime) - new Date(a.createTime);

            } else if (sorting === "oldest") {
                return new Date(a.createTime) - new Date(b.createTime);
            } else if (sorting === "highPrice") {
                return b.price - a.price;
            } else if (sorting === "lowPrice") {
                return a.price - b.price;
            }
        });
        // Filter the sorted products array based on the selected category
        const filteredProducts = category === "" ? sortedProducts : sortedProducts.filter(product => product.category === category);

        setDisplayProducts(filteredProducts);

        setCurrentPage(1)

    }, [products, sorting, category]);

    return (
        <div className="Products">
            <div className="Products-sideBar">
                <h2></h2>
                <div className="Products-sideBar-selectorContainer">
                    <Box sx={{ minWidth: 120 }} className="Products-sideBar-selector">
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label"></InputLabel>
                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                value={category}
                                label="category"
                                onChange={handleCategoryChange}
                            >
                                <MenuItem value=""></MenuItem>
                                <MenuItem value=""></MenuItem>
                                <MenuItem value=""></MenuItem>
                                <MenuItem value=""></MenuItem>
                            </Select>
                        </FormControl>
                    </Box>
                    <Box sx={{ minWidth: 120 }} className="Products-sideBar-selector">
                        <FormControl fullWidth>
                            <InputLabel id="demo-simple-select-label"></InputLabel>
                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                value={sorting}
                                label="sorting"
                                onChange={handleSortingChange}
                            >
                                <MenuItem value="newest"></MenuItem>
                                <MenuItem value="oldest"></MenuItem>
                                <MenuItem value="highPrice">()</MenuItem>
                                <MenuItem value="lowPrice">()</MenuItem>
                            </Select>
                        </FormControl>
                    </Box>
                </div>
            </div>

            <div className="Products-content">
                <div className="Products-content-headers">
                    <h2></h2>
                    {/* sorting box */}

                </div>
                <div className="Products-content-list">
                    {displayProducts.slice(currentPage * 9 - 9, currentPage * 9).map(product => (
                        <LittleProduct {...product} key={product._id} />
                    ))}
                </div>
                <div className="Products-pagination">
                    <Pagination count={pagesNum} variant="outlined" shape="rounded" onChange={handlePageChange} />
                </div>
            </div>
        </div>
    )
}

export default Products;