import React, { useState } from "react";
import TransitionAlerts from '../helperComponent/TransitionAlerts'

import { useNavigate } from 'react-router-dom';
import useInputState from '../hooks/useInputState'
import TextField from '@mui/material/TextField';

import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Button from '@mui/material/Button';
import { MuiFileInput } from 'mui-file-input'

import './NewProduct.css'


function NewProduct() {
    const [name, handleNameChange, resetName] = useInputState("")
    const [price, handleNamePrice, resetPrice] = useInputState("")
    const [description, handleDescriptionChange, resetDescription] = useInputState("")
    const [images, handleImages, resetImages] = useInputState("")
    const [category, setCategory] = useState("");
    const [errorMsg, setErrorMsg] = useState("")
    const [open, setOpen] = useState(false);
    const handleCategoryChange = (event) => {
        setCategory(event.target.value);
    };
    const [files, setFiles] = useState(null)

    const handleChange = (newFile) => {
        if (newFile) {
            const selectedFiles = Array.from(newFile);
            const allowedTypes = ['image/png', 'image/jpeg', 'image/jpg'];

            const validatedFiles = selectedFiles.filter((file) =>
                allowedTypes.includes(file.type)
            );

            setFiles(validatedFiles);
        }
    };

    const history = useNavigate();

    const handleSubmit = (e) => {
        e.preventDefault();

        if (!(name && price && description && files && category)) {
            setOpen(true)
            setErrorMsg(`: ${!name ? " " : ""}${!price ? " " : ""}${!files ? " " : ""}${!category ? " " : ""}${!description ? " " : ""}`)
            return;
        }
        let formData = new FormData();
        formData.append('name', name);
        formData.append('price', price);
        formData.append('category', category);
        formData.append('description', description);
        for (let i = 0; i < files.length; i++) {
            formData.append('images', files[i]);
        }
        fetch("/api/products/new",
            {
                body: formData,
                method: "POST"
            })
            // fetch('/api/products/new', {
            //     method: 'POST',
            //     headers: {
            //         'Content-Type': 'application/json',
            //     },
            //     body: JSON.stringify({ name, price, category, description, images }),
            // })
            .then((response) => response.json())
            .then((data) => {
                console.log('Success:', data);
                if (data.message !== "you must be signed in") {
                    history(`/product/${data.product._id}`);
                    history(0);
                }
            })
            .catch((error) => {
                console.error('Error:', error);
            });
    }
    return (
        <div>
            <div className="alert">
                <TransitionAlerts messege={errorMsg} open={open} setOpen={setOpen} />
            </div>
            <form onSubmit={handleSubmit}>
                <TextField
                    value={name}
                    onChange={handleNameChange}
                    id="name"
                    label=""
                    variant="outlined"
                    autoFocus
                />
                <TextField
                    type="number"
                    value={price}
                    onChange={handleNamePrice}
                    id="price"
                    label=""
                    variant="outlined"
                />
                {/* file input */}
                <br />
                <MuiFileInput value={files} onChange={handleChange} multiple placeholder="" margin="normal" />

                <Box sx={{ minWidth: 120 }} className="Products-sideBar-selector">
                    <FormControl fullWidth>
                        <InputLabel id="demo-simple-select-label"></InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={category}
                            label="category"
                            onChange={handleCategoryChange}
                        >
                            <MenuItem value=""></MenuItem>
                            <MenuItem value=""></MenuItem>
                            <MenuItem value=""></MenuItem>
                        </Select>
                    </FormControl>
                </Box>
                <TextField
                    value={description}
                    onChange={handleDescriptionChange}
                    id="description"
                    label=""
                    multiline
                    rows={4}
                    fullWidth
                    margin="normal"
                />
                <Button onClick={handleSubmit} variant="contained" margin="normal" fullWidth></Button>

            </form>

        </div>
    )
}

export default NewProduct;